import React from 'react';
import Button from '../../../Button/Button';
import './style.scss';

export const Warning = (props) => {
  return (
    <div
    className={`warning ${(props.state.modal == false) ? '' : 'active'}`}>
      <div className='warning__btns-block'>
        <h1 className='warning__btns-block__head'>
           Подтвердить заказ?
        </h1>
        <span>
          <Button
            title='Подтвердить'
            type='warn-btn'
            action={() => props.cb({ ...props.state, modal: false})}
          ></Button>
          <Button
            title='Вернуться'
            type='warn-btn red-btn'
            action={() => props.cb({ ...props.state, modal: false})}
          />
        </span>
      </div>
    </div>
  );
};
