/* eslint-disable react/jsx-key */
/* eslint-disable max-len */
import React, {useEffect, useState} from 'react';
import './style.scss';
import CustomInput from '../../../Custom/CustomInput';
import CustomCheckbox from '../../../Custom/CustomCheckbox';
import CustomRadio from '../../../Custom/CustomRadio';
import { RATE } from '../../../../service/urls';
import fetchData from '../../../../service/getData';
export const StepThree = (props) => {
  const [Rates, setRates] = useState({ data: []});

   useEffect(async () => {
    const resRates = fetchData(RATE);

    setRates(await resRates);
   }, []);

  const setOptions = (value) => {
    const State = { ...props.state };

    const index = State.options.findIndex((elem) => elem == value);

    if (index != -1) {
      State.options.splice(index, 1);
    } else {
      State.options.push(value);
    }

    props.cb(State);
  };

  return (
    <div className="step-three">
      <form className="step-three__form">
        <fieldset className="step-three__form__color-selector">
          <legend>Цвет</legend>
          <br></br>
          <CustomRadio
            type="radio"
            name="Цвет"
            value="Любой"
            checked={true}
            description="Любой"
            action={(e) => props.cb({ ...props.state, color: e.target.value })}
          />
          <CustomRadio
            type="radio"
            name="Цвет"
            value="Красный"
            checked={false}
            description="Красный"
            action={(e) => props.cb({ ...props.state, color: e.target.value })}
          />
          <CustomRadio
            type="radio"
            name="Цвет"
            value="Голубой"
            checked={false}
            description="Голубой"
            action={(e) => props.cb({ ...props.state, color: e.target.value })}
          />
        </fieldset>
        <fieldset className="step-three__form__date-selector">
          <legend>Дата аренды</legend>
          <CustomInput
            name={'С'}
            type="datetime-local"
            onChangeAction={(e) => {
              props.cb({ ...props.state, rent_start: Date.parse(e.target.value) });
            }}
          />
          <br />
          <CustomInput
            name={'До'}
            type="datetime-local"
            onChangeAction={(e) => {
              props.cb({ ...props.state, rent_end: Date.parse(e.target.value) });
            }}
          />
        </fieldset>
        <fieldset className="step-three__form__tariff">
          <legend>Тариф</legend>
          <br />
          {Rates.data.map((el)=>{
            return (
            <><CustomRadio
                type="radio"
                name="Тариф"
                key={el.id}
                value={el.rateTypeId.name}
                checked={false}
                description={`${el.rateTypeId.name}, ${el.price}₽ ${el.rateTypeId.unit}`}
                action={(e) => props.cb({ ...props.state, tarif: e.target.value })} /><br /></>);
          })}
        </fieldset>
        <fieldset className="step-three__form__additional">
          <legend>Дополнительные услуги</legend>
          <br />
          <CustomCheckbox
            type="checkbox"
            name="Опции"
            value="Полный бак"
            key={1}
            description="Полный бак, 500р"
            action={(e) => setOptions(e.target.value)}
          />
          <br />
          <CustomCheckbox
            type="checkbox"
            name="Опции"
            key={2}
            value="Детское кресло"
            description="Детское кресло, 200р"
            action={(e) => setOptions(e.target.value)}
          />
          <br />
          <CustomCheckbox
            type="checkbox"
            name="Опции"
            key={3}
            value="Правый руль"
            description="Правый руль, 1600р"
            action={(e) => setOptions(e.target.value)}
          />
        </fieldset>
      </form>
    </div>
  );
};
