/* eslint-disable new-cap */
/* eslint-disable max-len */
/* eslint-disable react/react-in-jsx-scope */
import { useState } from 'react';
import Header from '../../Header/Header';
import Total from './Total/Total';
import './style.scss';
import { StepOne } from './StepOne/StepOne';
import { StepTwo } from './StepTwo/StepTwo';
import {StepThree} from './StepThree/StepThree';
import {StepFour} from './StepFour/StepFour';
import { NavElement } from './NavElement';
import { Warning } from './Total/Warning';


export const OrderPage = () => {
    const [State, setState] = useState({
      s1: true,
      s2: false,
      s3: false,
      s4: false,
      c1: false,
      c2: false,
      c3: false,
      city: '',
      destination: '',
      car: '',
      color: '',
      rent_start: '',
      rent_end: '',
      tarif: '',
      options: [],
      price_start: 0,
      price_end: 0,
      number: '',
      thumbnail: '',
      points: [],
      modal: false,
    });

    const navigation = [
      { value: 1, description: 'Местоположение' },
      { value: 2, description: 'Модель' },
      { value: 3, description: 'Дополнительно' },
      { value: 4, description: 'Итого' },
    ];

    const NavPrev = (index) => {
      switch (index) {
        case 1: setState({ ...State, s1: true, s2: false, s3: false, s4: false, c1: false, c2: false, c3: false }); break;
        case 2: setState({ ...State, s1: false, s2: true, s3: false, s4: false, c1: true, c2: false, c3: false }); break;
        case 3: setState({ ...State, s1: false, s2: false, s3: true, s4: false, c1: true, c2: true, c3: false }); break;
      }
    };

    return (
    <>
        <Header classNames='header order-page_header' />
      <div className='order-page'>
      <div className='order-page__nav'>
      {navigation.map((el, i) => (
              <NavElement
                key={i}
                value={el.value}
                description={el.description}
                state={ State }
                action={ () => NavPrev(el.value) }
              />
            ))}
        </div>
        <div className='order-page__container'>
          <section className='order-page__container__form'>
            { State.s1 ? <StepOne state={ State } cb={ setState }/> : null }
            { State.s2 ? <StepTwo state={ State } cb={ setState }/> : null }
            { State.s3 ? <StepThree state={ State } cb={ setState }/> : null }
            { State.s4 ? <StepFour state={ State } cb={ setState }/> : null }
          </section>
          <section className='order-page__container__total'>
            <Total state={ State } cb={ setState }/>
            <Warning state={ State } cb={ setState }/>
          </section>
        </div>
      </div>
      </>
    );
};
