/* eslint-disable camelcase */
import React, { useState, useEffect } from 'react';
import './style.scss';
import FakeMap from '../../../../assets/FakeMap.jpg';
import CustomInput from '../../../Custom/CustomInput';
import fetchData from '../../../../service/getData';
import { CITIES, POINTS } from '../../../../service/urls';


export const StepOne = (props) => {
   const [Points, setPoints] = useState({ data: []});
   const [Cities, setCities] = useState({ data: []});


   useEffect(async () => {
    const resPoints = fetchData(POINTS);
    const resCities = fetchData(CITIES);
    setPoints(await resPoints);
    setCities(await resCities);
   }, []);

   const filterPoints = (city_name) => {
      const P = Points.data.filter((elem) => elem.cityId?.name == city_name);

      props.cb({...props.state, city: city_name, points: P});
   };

  return (
      <div className='step-one'>
        <form className='step-one__form' action=''>
          <CustomInput
            name={'Город'}
            type='text'
            placeholder='Введите название города'
            list={Cities.data}
             onChangeAction={ (e) => filterPoints(e.target.value) }
            value={ props.state.city }
          />
          <datalist id={Cities.data}>
            {Cities.data.map((city)=>{
              return (
              <option
              key={city.id}
              value={city.name}
              />
              );
            })}
          </datalist>
            <br />
          <CustomInput
            name={'Пункт Выдачи'}
            type='text'
            placeholder='Начните вводить пункт...'
            list={props.state.points}
            onChangeAction={ (e) =>
              props.cb({ ...props.state, destination: e.target.value })}
            value={ props.state.destination }
          />
          <datalist id={props.state.points}>
          {props.state.points.map((point)=>{
              return (
              <option
              key={point.id}
              value={point.name ? point.name : 'Отстань'}
              />
              );
            })}
          </datalist>
        </form>
        <div className='map-block'>
          <p className='map-block__description'>Выбрать на карте:</p>
          <span>
            <img className='map' src={FakeMap} alt='Карта Города' />
          </span>
        </div>
      </div>
    );
  };
