/* eslint-disable max-len */
import React from 'react';
export const NavElement = ({
    value,
    action,
    description,
    state,
  }) => {
;
  return (
      <button
        className={`order-page__nav__btn ${ (state.c1 && value == 1) || (state.c2 && value == 2) || (state.c3 && value == 3) ? 'completed' : ''}`}
        name='currentStep'
        value={value}
        onClick={action}
        disabled={ (!state.c1 && value == 1) || (state.c1 && value == 1) || (state.c1 && value == 2) || (state.c2 && value == 3) || (state.c3 && value == 4) ? false : true }
      >
        {description} {value !== 4 && <span> ► </span>}
      </button>
  );
  };
