/* eslint-disable max-len */
import React from 'react';
import Button from '../../../Button/Button';
import './style.scss';

export default function Total(props) {
  const getHumanizedValue = (diffInHours) => {
    const days = Math.floor(diffInHours / (1000 * 60 * 60 * 24) % 30);
    const hours = Math.floor((diffInHours / (1000 * 60 * 60)) % 24);

    return `${days} д ${hours} ч`;
  };
  return (
    <div className='total'>
      <h1 className='total__head'>Ваш заказ:</h1>
      <div className='total__list'>
        <p className='total__list__item'>
          <span className='text'>Пункт выдачи</span>
          <span className='dots'></span>
          <span className='text__dinamic'>
             { props.state.city }
            <br /> { props.state.destination }
          </span>
        </p>
        <p className='total__list__item'>
          <span className='text'>Модель</span> <span className='dots'></span>
          <span className='text__dinamic'>{ props.state.car }</span>
        </p>
        <p className='total__list__item'>
          <span className='text'>Цвет</span> <span className='dots'></span>
          <span className='text__dinamic'>{props.state.color}</span>
        </p>
        <p className='total__list__item'>
          <span className='text'>Длительность аренды</span>
          <span className='dots'></span>
          <span className='text__dinamic'> {getHumanizedValue(props.state.rent_end-props.state.rent_start)}</span>
        </p>
        <p className='total__list__item'>
          <span className='text'>Тариф</span> <span className='dots'></span>
          <span className='text__dinamic'>{props.state.tarif}</span>
        </p>
          {props.state.options.map((option) => {
            return ( <>
            <p className='total__list__item'>
              <span className='text'>{option}</span>
              <span className='dots'></span>
              <span className='text__dinamic'>Да</span>
              </p>
              </>
            );
})}
        <p className='total__sum'>
          <span>Цена: от { props.state.price_start } до { props.state.price_end } Р</span>
        </p>

        { props.state.s1 ?
        <Button
        type='button big-btn total-btn'
        title='Выбрать модель'
        action={ () => props.cb({ ...props.state, s1: false, s2: true, c1: true, c2: false, c3: false })}
        disable={ props.state.city && props.state.destination ? false : true } /> : null
        }

        { props.state.s2 ?
        <Button
        type='button big-btn total-btn'
        title='Дополнительно'
        action={ () => props.cb({ ...props.state, s2: false, s3: true, c1: true, c2: true, c3: false })}
        disable = {props.state.car ? false : true}/> : null
        }
        { props.state.s3 ?
        <Button
        type='button big-btn total-btn'
        title='Итого'
        action={ () => props.cb({ ...props.state, s3: false, s4: true, c1: true, c2: true, c3: true })}
        disable = {props.state.rent_end && props.state.rent_start ? false : true}/> : null }
        { props.state.s4 ?
        <Button
        type='button big-btn total-btn'
        title='Заказать'
        action={() => props.cb({ ...props.state, modal: true})}
        /> : null }
      </div>
    </div>
  );
}
